import React, { useState, useEffect } from 'react';
import { Icon, Row, Col } from 'antd';
import 'moment/locale/en-gb';
import { BtnAddNew } from 'components/common/button';
import moment from '../../../../node_modules/moment-timezone/index';
import Card from '../../../components/common/Card/index';
import CardTitle from '../../../components/common/CardTitle/index';

import './styles.scss';
import { getStudentTimetable } from '../../../services/educationPortal';
import useCurrentUserId from '../../../hook/useCurrentUserId';
import TableData from '../../../react-uikit/component/table/TableData';
import { dateToStringParamReq } from '../../../utils/formatUtil';

import ModalRegisterRange from './modalRegisterRange';

const lessons = Array.from({ length: 10 }, (_, i) => i + 1);

const ddVietsub = {
  Monday: 'Thứ 2',
  Tuesday: 'Thứ 3',
  Wednesday: 'Thứ 4',
  Thursday: 'Thứ 5',
  Friday: 'Thứ 6',
  Saturday: 'Thứ 7',
  Sunday: 'CN',
};

moment.locale('en-gb');
export default () => {
  const [fromDate, setFromDate] = useState();
  const [showExportNoteModal, setShowExportNoteModal] = useState(false);
  const studentId = useCurrentUserId();
  useEffect(() => {
    if (fromDate) {
      return;
    }
    setFromDate(moment().startOf('week'));
  }, []);
  if (!fromDate) {
    return null;
  }
  const toDate = moment(fromDate).add(6, 'days');
  const paramSearch = {
    studentId,
    fromDate: dateToStringParamReq(fromDate),
    toDate: dateToStringParamReq(toDate),
  };
  const tableColumns = [
    {
      title: 'Tiết',
      key: 'lesson',
      dataIndex: 'lesson',
      width: 80,
      align: 'center',
    },
  ];
  for (let m = moment(fromDate); m.diff(toDate, 'days') <= 0; m.add(1, 'days')) {
    const studyDateKey = moment(m).format('YYYY-MM-DDT00:00:00');
    tableColumns.push({
      title: ddVietsub[m.format('dddd')],
      key: m.format('dddd'),
      render: (text, record) => {
        if (!record.data[studyDateKey]) {
          return '';
        }
        const subject = record.data[studyDateKey].find(item => item.fromPeriodId === record.lesson);
        if (!subject) {
          return '';
        }
        const rowSpan = subject.toPeriodId + 1 - subject.fromPeriodId;
        const obj = {
          children: (
            <div className="subject-item">
              <p className="subject-name">{subject.subject}</p>
              <p>{subject.room}</p>
              <p>{subject.teacher}</p>
            </div>
          ),
          props: {
            rowSpan,
          },
        };
        return obj;
      },
    });
  }
  const handleGetDataResponse = res => {
    const [data] = res;
    const { items } = data.data;
    const dataTable = items.reduce((obj, item) => {
      if (!obj[item.studyDate]) {
        obj[item.studyDate] = [];
      }
      obj[item.studyDate].push(item);
      return obj;
    }, {});
    return { content: lessons.map(l => ({ lesson: l, data: dataTable })), total: lessons.length };
  };

  const addRegisterRange = async () => {
    setShowExportNoteModal(true);
  }

  const hideExportNoteModal = () => {
    setShowExportNoteModal(false);
  }

  return (
    <div className="page-Timetable">
      <Card>
        <CardTitle title="Thời khóa biểu" />
        <Row>
          <Col span={12}>
            <BtnAddNew onClick={() => addRegisterRange()} />
          </Col>
        </Row>
        <div className="btn-move-container">
          <button
            className="btn-move"
            onClick={() => {
              setFromDate(moment(fromDate).add(-7, 'days'));
            }}
          >
            <Icon type="left-circle" />
          </button>
          <div>
            <strong>{fromDate.format('DD/MM/YYY')}</strong>
            <span> - </span>
            <strong>{toDate.format('DD/MM/YYY')}</strong>
          </div>
          <button
            className="btn-move"
            onClick={() => {
              setFromDate(moment(fromDate).add(7, 'days'));
            }}
          >
            <Icon type="right-circle" />
          </button>
        </div>
        <TableData
          allowGetData={!!studentId}
          handleGetDataResponse={handleGetDataResponse}
          paramSearch={paramSearch}
          columns={tableColumns}
          service={getStudentTimetable}
          pagination={false}
        />
      </Card>
      <ModalRegisterRange
        visible={showExportNoteModal}
        onSuccess={hideExportNoteModal}
        onCancel={hideExportNoteModal}
      />
    </div>
  );
};



